.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;

  opacity: 1;
  transition: opacity var(--animation), visibility var(--animation);
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  min-height: 581px;
  min-width: 528px;

  background-color: var(--primary-color);
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
}

.modal__btn {
  position: absolute;
  top: 8px;
  right: 8px;

  background: var(--primary-color);
  border: 1px solid rgba(0, 0, 0, 0.1);
  width: 30px;
  height: 30px;
  border-radius: 50%;
  cursor: pointer;
  color: var(--primary-text-color);

  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.modal__icon {
  fill: currentColor;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.modal__btn:hover,
.modal__btn:focus {
  //   color: var(--accent-color);
  color: green;
}

.form {
  display: flex;
  flex-direction: column;
  min-width: 448px;
  padding: 40px;
}

.form__title {
  font-weight: 700;
  font-size: 20px;
  line-height: 1.15;
  text-align: center;
  color: var(--title-text-color);
  margin-bottom: 12px;
}

.form-group {
  position: relative;

  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}

.form-group__comment {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

textarea {
  resize: none;
}

.form-group__label {
  font-size: 12px;
  line-height: 1.17;
  letter-spacing: 0.01em;
  color: var(--primary-text-color);
  margin-bottom: 4px;
}

.form-group__contact {
  font-size: 12px;
  line-height: 1.17;
  letter-spacing: 0.01em;
  color: var(--primary-text-color);
  cursor: pointer;
  width: 100%;

  border: 1px solid rgba(33, 33, 33, 0.2);
  box-sizing: border-box;
  border-radius: 4px;
  outline: none;

  transition: border-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.form-input {
  height: 40px;
  padding-left: 42px;
  margin: 0;
}

.form-group__contact:focus + .form-group__icon,
.form-group__contact:focus {
  border-color: var(--accent-color);
  fill: var(--accent-color);
}

.form-group__icon {
  position: absolute;
  top: 50%;
  left: 12px;

  width: 18px;
  height: 18px;
  fill: var(--primary-text-color);

  transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.form__textarea {
  height: 120px;
  padding: 12px 14px;
}

.form__textarea::placeholder {
  color: rgba(117, 117, 117, 0.5);
  color: 1px solid var(--primary-text-color);
}

.form__checkbox-label {
  display: flex;
  align-items: center;
  justify-content: center;

  font-size: 14px;
  line-height: 1.71;
  color: var(--primary-text-color);
  margin-bottom: 30px;
}

.form__checkbox-label::before {
  content: '';
  display: inline-block;
  width: 16px;
  height: 16px;
  border: 1.8px solid var(--primary-text-color);
  border-radius: 3px;
  margin-right: 8px;
}

.form__checkbox:checked + .form__checkbox-label::before {
  background-image: url(../images/icons/svg/checkbox.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  background-color: var(--accent-color);
  border: var(--accent-color);
}

.form__checkbox:focus + .form__checkbox-label::before {
  box-shadow: inherit;
}

.form__checkbox-link {
  color: var(--accent-color);
  margin-left: 4px;
}

.button-modal {
  @extend %button;

  display: block;
  margin-right: auto;
  margin-left: auto;

  font-weight: 700;
  font-size: 16px;
  line-height: 1.31;

  width: 161px;
  height: 44px;

  color: var(--primary-color);
  background-color: var(--accent-color);

  stroke: var(--primary-color);

  transition: background-color 250ms var(--animation);

  &:hover,
  &:focus {
    background-color: var(--second-bg);
  }
}
